<template>
    <div id="Header">
        <nav id="navBar" class="navbar  navbar-expand-sm ">
            <!-- Logo -->
            <a class="navbar-brand col-4 inline" href="/">
                <img src="@/assets/logo2.png" alt="VietSpeech School" style="width:250px;">
            </a>
            <!-- <p style="width: 10px;background-color: red;">{{ $route.path }}</p> -->
            <!-- Links -->
            <ul class="col-8 navbar-nav inline">
                <div class="col-9 d-flex justify-content-around inline">
                    <li class="align-self-center nav-item" v-for="item in listHeader" :key="item">
                        <a>{{ item.message }}</a>
                    </li>
                </div>
                <!-- Dropdown -->
                <div class="col-3 nav-item dropdown">
                    <button class="btn btn-ligh  d-inline-flex"
                        @click="showAccountDropdown = !showAccountDropdown">
                        <img id="avatar" class="rounded-circle" :src="getUserAvatar()" />
                        <a class=" nav-link text-center dropdown-toggle" href="#" id="navbardrop"
                            data-toggle="dropdown">{{ Name }}</a>
                    </button>
                    <div v-show="showAccountDropdown" class="dropdownList">
                        <a class="text-center" @click="this.chooseInformation()">Tài khoản</a>
                        <a class="text-center" @click="this.changePassword()">Đổi mật khẩu</a>
                        <a class="text-center" @click="showConfirmModal(true)">Đăng xuất</a>
                    </div>
                </div>
            </ul>
        </nav>
        <Teleport to="body">
            <DialogConfirmVue v-show="this.isShowConfirm" v-on:onClickEventModal="this.onClickConfirmEvent" :Title="'Xác nhận đăng xuất'" :content="'Bạn chắc chắn muốn đăng xuất?'"></DialogConfirmVue>
        </Teleport>
    </div>
</template>

<script>
import DialogConfirmVue from "@/components/Modal/DialogConfirm.vue";
import { LocalStorageKey } from '@/constant/LocalStorageKey';
import router from '@/router/Router';


export default {
    name: "DashboardHeader",
    components: {
        DialogConfirmVue
    },
    methods: {
        chooseInformation() {
            router.push({ path: "/information" })
            this.showAccountDropdown = false
        },

        changePassword() {
            // do later
        },

        showConfirmModal(value) {
            this.isShowConfirm = value
            document.getElementsByTagName("body")[0].classList.add("modal-open");
        },

        onClickConfirmEvent(newShowConfirm) {
            this.isShowConfirm = newShowConfirm
            if (newShowConfirm == true) {
                document.getElementsByTagName("body")[0].classList.remove("modal-open");
                this.logout()
            } else {
                document.getElementsByTagName("body")[0].classList.remove("modal-open");
            }
        },

        getUserAvatar() {
            if (this.$cookies.get(LocalStorageKey.ACC_AVATAR) === 'null') {
                return require('@/assets/avatar-demo.jpg')
            } else {
                return this.$cookies.get(LocalStorageKey.ACC_AVATAR)
            }
        },

        logout() {
            this.$cookies.keys().forEach(cookie => this.$cookies.remove(cookie))
            this.$store.commit('setIsLogin', false)
            router.push({ name: "home" })
        }
    },
    props: {
    },
    data() {
        return {
            listHeader: [{ message: 'Trang quản lý' }],
            Name: this.$cookies.get(LocalStorageKey.ACC_NAME),
            showAccountDropdown: false,
            isShowConfirm: false,
        }
    }
};
</script>

<style>
.dropdownList a {
    display: block;
    padding: 5px;
    font-size: 20px;
    text-decoration: none;
    color: black;
}

.dropdownList a:hover {
    background-color: aquamarine;
    color: black;
    cursor: pointer;
}

.dropdownList {
    position: absolute;
    background-color: white;
    width: 200px;
    padding: 10px;
    border-radius: 15px;
    border: #ccc 1px solid;
    z-index: 1;

}

#navBar {
    padding: 0px;
}

.inline {
    display: inline-block;
}

.navbar-brand {
    margin: 0 !important;
}

#navbar ul {
    display: inline-block;
}

#navBar li {
    justify-items: center;
    text-align: center;
    justify-content: center;
    vertical-align: middle;
    align-items: center;
}

.padding0_5 {
    padding-top: 1.25rem;
}

#navBar li a {

    vertical-align: middle;
}

#avatar {
    width: 60px;
}
</style>
