<template>
    <div>
        <div class="node margint-5 marginb-5">
            <div class="d-flex" :style="{ 'margin-left': `${depth * 30}px` }">
                <!-- test -->
                <li :class="`li-${depth}`">
                    <input type="text" @blur="handleBlur(inputValue)" :value="inputValue"
                        @input="event => inputValue = event.target.value/* update on input */"
                        placeholder="input something pls...">
                </li>
                <!-- some prolem with length if plans is gone  -->
                <div v-if="Array.isArray(curNode.plans)"><!--check if plans exist-->
                    <div v-if="(curNode.plans.length <= 0 && depth < 3)">
                        <button @click="clickAddplans">add plans</button>
                    </div>
                </div>
            </div>
        </div>
        <!-- recusive start here -->
        <ol>
            <TreeThing v-for="plans in curNode.plans" :key="plans" v-on:EmitAddMore="catchEmitAddMore"
                v-on:EmitDelete="catchEmitDelete" :node="plans" :depth="(depth + 1)"
                :lastObject="node.plans[node.plans.length - 1]" />
        </ol>
        <div class="margint-5 marginb-5">
            <div class="d-flex" :style="{ 'margin-left': `${depth * 30}px` }">
                <button @click="clickAddMore" v-if="(depth > 0 && curNode == lastObject)">add more</button>
            </div>
        </div>
    </div>
</template>

<script>
export default {
    name: "TreeThing",
    props: {
        node: Object,
        depth: {
            type: Number,
            default: 0,
        },
        lastObject: {//the last object 
            type: Object,
            default: () => { },//object default is function of constructer
        }
    },
    methods: {
        clickAddMore() {
            this.$emit("EmitAddMore")
        },
        clickAddplans() {
            this.curNode.plans.push({ name: "add plans", plans: [] })
        },
        catchEmitAddMore() {
            this.curNode.plans.push({ name: "add more", plans: [] })
        },
        catchEmitDelete(somenode) {
            console.log(somenode)
            //remove data in front end
            const index = this.curNode.plans.indexOf(somenode);
            if (index > -1) { // only splice array when item is found
                this.curNode.plans.splice(index, 1); // 2nd parameter means remove one item only
            }
        },
        handleBlur(newInputValue) {
            //update the new input value to the tree
            this.curNode.planContent = newInputValue
            //check if input empty
            if (this.curNode.planContent) {
                console.log("you lost focus")
            } else {

                this.$emit("EmitDelete", this.curNode)
            }
        }
    },
    data() {
        return {
            curNode: this.node,
            inputValue: this.node.planContent,
        }
    }

}
</script>

<style scoped>
.node {
    text-align: left;
    min-height: 30px;
}

.margint-5 {
    margin-top: 5px;
}

.marginb-5 {
    margin-bottom: 5px;
}

.d-flex {
    display: flex;
    align-items: center;
    gap: 10px;
}

/* style for button */
button {
    height: 30px;
    border-radius: 10px !important;
    border: solid 1px gray;
    padding: 5px;
}

button:active {
    background-color: rgb(142, 163, 169);
}

/* input style  */
input {
    min-height: 30px;
    border: none;
    border-radius: inherit;
}

input:focus {
    outline: none;
}

/* simple css for list */
ol {
    list-style-position: inside;
}

li {
    border-radius: 10px;
    border: solid 1px gray;
    padding-left: 5px;
    background-color: white;
}

li:focus-within {
    border: solid 2px gray;
}

/* style for each level of input */
li {
    list-style-type: disc;
}

li.li-0 {
    list-style-type: none;
}

li.li-1 {
    list-style-type: decimal;
    font-family: Baskerville;
}

li.li-2 {
    list-style-type: lower-alpha;
}
</style>