<template>
  <div class = "tab_bar">
    <h4 class="col-8" style="display: inline-block">Danh sách đáng giá trẻ:</h4>
  </div>
  <div>
    <div class="ItemList d-flex justify-content-between" v-for="rep in this.listAssessment" :key="rep">
      <p class=""> Đánh giá trẻ ngày {{ rep.createdDate || "14/11/2022"}}</p>
      <button type="btn" class="btn btn-light" @click="openAssessmentModal()">View</button>
    </div>
  </div>
  <Paging :totalPage="this.totalPage" 
          :currentPage = "this.currentPage"
          v-show="this.isShowPaging" 
          v-on:onClickToPage="onClickToPage" 
          id="paging">
  </Paging>
  <AssessmentDetailModal v-show="this.isShowAssessmentModal" v-on:onclose="closeAssessmentModal()"></AssessmentDetailModal>
</template>

<script>
import { FetchApiWithCustomHeader } from "@/api/APIFetch";
import { ContentType, Method, StudentAPI  } from "@/api/ListAPI";
import { LocalStorageKey } from "@/constant/LocalStorageKey";
import Paging from "@/components/Paging.vue";
import AssessmentDetailModal from './AssessmentDetailModal.vue';
import router from "@/router/Router";


export default {
  name: "StudentAssessment",
  components: {
    Paging,
    AssessmentDetailModal
  },
  methods: {
    updateIsLoading(loadingState) {
      this.$store.commit("setIsLoading", loadingState);
    },

    onClickToPage(pageIndex) {
      this.currentPage = pageIndex
      router.push( { path: `assssment-report/${this.$route.params.id}`, query: { page: pageIndex} } )
    },

    updateListAssessment(newListAssessment) {
      this.listAssessment = [...newListAssessment];
    },

    updateTotalPage(newTotalPage) {
      if (newTotalPage == 1) {
        this.isShowPaging = false;
      } else {
        this.isShowPaging = true;
        this.totalPage = newTotalPage;
      }
    },

    openAssessmentModal() {
      this.isShowAssessmentModal = true;
      document.getElementsByTagName("body")[0].classList.add("modal-open");
    },

    closeAssessmentModal() {
      this.isShowAssessmentModal = false;
      document.getElementsByTagName("body")[0].classList.remove("modal-open");
    },

    initCurrentPage() {
        if (this.$route.query.page !== "0" && this.$route.query.page > 0  && this.$route.query.page <= this.totalpage) {
            this.currentPage = this.$route.query.page
        } else {
            this.currentPage = 1
        }
    },

    getListAssessment() {
      let tokenType = this.$cookies.get(LocalStorageKey.TOKEN_TYPE_KEY);
      let token = this.$cookies.get(LocalStorageKey.TOKEN_KEY);
      this.updateIsLoading(true);
      FetchApiWithCustomHeader(
        StudentAPI.GetAssessmentByStudent +`/${this.$route.params.id}/${this.currentPage - 1}/3?search=`,
        Method.GET,
        {
          Content_Type: ContentType.JSON,
          Authorization: `${tokenType} ${token}`,
        },
        null,
        null,
        (response) => {
          if (response.result === "OK") {
            this.updateListAssessment(response.data.resultList);
            this.updateTotalPage(response.data.totalPage);
            this.initCurrentPage()
          } else {
            // do something
          }
          this.updateIsLoading(false);
        }
      );
    },
  },
  created() {
    this.getListAssessment();
  },
  data() {
    return {
      listAssessment: [],
      searchContent: this.$route.query.search || "",
      currentPage: this.$route.query.page || 1,
      totalPage: 0,
      isShowPaging: false,
      isShowAssessmentModal: false,
    };
  },
};
</script>

<style scoped>
.ItemList {
  background-color: white;
  border: 1px solid #ccc;
  margin: 15px 10px;
  border-radius: 10px;
  padding: 5px;
}

.ItemList p {
  margin-top: auto !important;
  margin-bottom: auto !important;
  margin-left: 20px;
}

.ItemList button {
  background-color: #e2e2e9;
  color: black;
  border: 1px solid #ccc;
  width: 10%;
}

.tab_bar {
  width: 100%;
  padding: 20px;
  padding-bottom: 0px;
}
</style>
