<template>
  <div class = "tab_bar">
    <h4 class="col-8" style="display: inline-block">Danh sách các kế hoạch can thiệp của trẻ:</h4>
  </div>
  <div class="col-12" v-show="!this.$store.state.isLoading">
      <div class="ItemList d-flex justify-content-between" v-for="rep in listReport" :key="rep">
          <p class="bolder">Kế hoạch can thiệp {{ rep.interventionPlanId }}</p>
          <p class="approve" v-show="rep.managerConfirmFlg">Chấp thuận</p>
          <p class="reject" v-show="!rep.managerConfirmFlg">Từ chối</p>
          <button type="btn" class="btn btn-light" @click="openPlanDetail(rep.interventionPlanId)">Xem chi tiết</button>
      </div>
      <Paging :totalPage="this.totalPage" :currentPage = "this.currentPage" v-show="this.isShowPaging" v-on:onClickToPage="onClickToPage" id="paging"></Paging>
      <CreateNewInventionReport v-show="this.isShowCreateNewReportModal" v-on:onclose="this.closeCreateNewReportModal"></CreateNewInventionReport>
  </div>

</template>
<script>
import Paging from '@/components/Paging.vue';
import CreateNewInventionReport from '@/components/Teacher/CreateNewInventionReport.vue'
import { FetchApiWithCustomHeader } from "@/api/APIFetch";
import { Method, StudentAPI, ContentType } from "@/api/ListAPI";
import { LocalStorageKey } from "@/constant/LocalStorageKey";
import router from '@/router/Router';

export default {
  name: "InterventionPlanReport",
  components: {
      Paging,
      CreateNewInventionReport
  },
  methods: {
      updateIsLoading(newLoadingState) {
          this.$store.commit("setIsLoading", newLoadingState);
      },

      openCreateNewReportModal() {
          this.isShowCreateNewReportModal = true;
          document.getElementsByTagName("body")[0].classList.add("modal-open");
      },

      openPlanDetail(planID) {
        router.push({path: `${this.$route.params.id}/intervention-plan-detail/${planID}`})
      },

      closeCreateNewReportModal() {
          this.isShowCreateNewReportModal = false;
          document.getElementsByTagName("body")[0].classList.remove("modal-open");
      },

      updateTotalPage(newTotalPage) {
          if (newTotalPage == 1) {
              this.isShowPaging = false
          }
          else {
              this.isShowPaging = true
              this.totalPage = newTotalPage;
          }
      },

      onClickToPage(pageIndex) {
        this.currentPage = pageIndex
        this.getInvertionPlanReport();
        router.push( { path: `parent/intervention-plan/${this.$route.params.id}` , query : { page: pageIndex}} )
      },

      updateInteventionPlanReportList(newListReport) {
          this.listReport = [...newListReport]
      },

      getInvertionPlanReport() {
          let tokenType = this.$cookies.get(LocalStorageKey.TOKEN_TYPE_KEY);
          let token = this.$cookies.get(LocalStorageKey.TOKEN_KEY);
          this.updateIsLoading(true);
          FetchApiWithCustomHeader(
          StudentAPI.GetStudentIntenventionPlanList +`/${this.$route.params.id}/${this.currentPage - 1}/3`,
          Method.GET,
          {
              Content_Type: ContentType.JSON,
              Authorization: `${tokenType} ${token}`,
          },
          null,
          null,
          (response) => {
              if (response.result === "OK") {
                console.log(response);
              this.updateInteventionPlanReportList(response.data.resultList);
              this.updateTotalPage(response.data.totalPage);
              this.initCurrentPage()
              } else {
                  // do something
               }
              this.updateIsLoading(false);
          });
      },

      initCurrentPage() {
            if (this.$route.query.page !== "0" && this.$route.query.page > 0  && this.$route.query.page <= this.totalPage) {
                this.currentPage = this.$route.query.page
            } else {
                this.currentPage = 1
            }
        }
  },
  created() {
      this.getInvertionPlanReport(0)
  },
  data() {
      return {
          listReport: [],
          totalPage: 0,
          currentPage: this.$route.query.page || 1,
          isShowPaging: false,
          isShowCreateNewReportModal: false,
      };
  },
}

</script>
<style scoped>
.ItemList {
    background-color: white;
    border: 1px solid #ccc;
    margin: 15px 10px;
    border-radius: 10px;
    padding: 5px;
}

.ItemList p {
    margin-top: auto !important;
    margin-bottom: auto !important;
    margin-left: 20px;
}

.ItemList button {
    background-color: #e2e2e9;
    color: black;
    border: 1px solid #ccc;
    width: 15%;
}

#paging {
    
    position: relative;
    bottom: 0px;
}

.approve {
    color: green;
}

.reject {
    color: red;
}

.tab_bar {
  margin: 20px;
}
</style>