<template>
    <div>
        <div class="container " style="margin-top: 1rem;">
            <div class="col-12 d-inline-flex">
                <p class="col-3  text-center"> Tìm kiếm học sinh :</p>
                <div class="col-7">
                    <input type="text " class="form-control" placeholder="Nhập tên hoặc nickname của học sinh ở đây" v-model = "this.searchContent" />
                </div>
                <div class="col-2 d-flex justify-content-between">
                    <button type="button" class="btn btn-info" @click="this.searchSupervisor()"> Tìm kiếm</button>
                </div>
            </div>
        </div>
        <div class="container">
            <div class="card-wrap" v-for="s in this.listSupervisor" :key="s">
                <button type="submit" class="btn card-custom d-flex flex-column" @click="">
                    <img :src="s.avatar || require('@/assets/avatar-demo.jpg')" class="rounded-circle" alt="">
                    <p class="text-center bolder">{{ s.fullName }}</p>
                    <p class="text-center">{{ s.nickName }}</p>
                </button>
            </div>

        </div>
        <Paging :totalPage="this.totalPage" v-if = "this.isShowPaging" :currentPage = "this.currentPage" v-on:onClickToPage="onMoveToPage"></Paging>
    </div>
</template>

<script>
import { FetchApiWithCustomHeader } from "@/api/APIFetch";
import { Method, AccountAPI, ContentType } from "@/api/ListAPI";
import { LocalStorageKey } from "@/constant/LocalStorageKey";
import Paging from '../Paging.vue';
import router from "@/router/Router";

export default {
    name: "StaffListSupervisor",
    components: {
        Paging
    },
    props: {

    },
    methods: {
        onClickToStudentId() {
            router.push( { path : '/Staff/supervisor-appointment'} )
        },

        getAllSupervisor() {
            let tokenType = this.$cookies.get(LocalStorageKey.TOKEN_TYPE_KEY);
            let token = this.$cookies.get(LocalStorageKey.TOKEN_KEY);
            this.updateIsLoading(true);
            FetchApiWithCustomHeader(
                AccountAPI.GetListAccountByRoleName + `/SUPERVISOR_MANAGER/${this.currentPage - 1}/5?search=${this.searchContent}`,
                Method.GET,
                {
                    Content_Type: ContentType.JSON,
                    Authorization: `${tokenType} ${token}`,
                },
                null,
                null,
                (response) => {
                    if (response.result === "OK") {
                        this.initCurrentPage()
                        this.updateTotalPage(response.data.totalPage)
                        this.updateSupervisorList(response.data.resultList)
                    } else {
                        console.log(response)
                        if (response.data.errorCode == "404" && response.data.errorMessage ==  "No data in list!") {
                            this.updateSupervisorList([])
                            this.updateTotalPage(1)
                            this.initCurrentPage()
                        }
                    }
                    this.updateIsLoading(false);
                }
            );
        },

        onMoveToPage(pageIndex) {
            this.currentPage = pageIndex
            router.push( {path : `supervisor-list` , query: { page: pageIndex, search: this.searchContent }})
        },

        udateSupervisorList(data) {
            this.listSupervisor = [...data]
        },

        updateTotalPage(newTotalPage) {
            if (newTotalPage == 1) {
                this.isShowPaging = false
            }
            else {
                this.isShowPaging = true
                this.totalPage = newTotalPage;
            }
        },

        updateIsLoading(newLoading) {
            this.$store.commit('setIsLoading', newLoading)
        },

        initCurrentPage() {
            if (this.$route.query.page !== "0" && this.$route.query.page > 0  && this.$route.query.page <= this.totalpage) {
                this.currentPage = this.$route.query.page
            } else {
                this.currentPage = 1
            }
        },

        searchSupervisor() {
            router.push( {path : `supervisor-list` , query: { page: this.currentPage, search: this.searchContent }})
            this.getAllSupervisor()
        }
    },
    created() {
        this.getAllSupervisor()
    },
    data() {
        return {
            listSupervisor: [],
            currentPage: this.$route.query.page || 1,
            searchContent: this.$route.query.search || "",
            totalPage: 0,
            isShowPaging: false
        }
    }
}
</script>
<style scoped>
.padding-top-5px {
    padding-top: 5px;
}

.card-wrap {

    display: inline-block;
    width: 25%;
    padding: 1.5rem;
}

.card-custom {
    border: 1px solid #ccc;
    border-radius: 5px;
    width: 100%;
    background-color: #e6e6e6;
}

button.card-custom img {
    width: 80%;
    margin-left: 10%;
    margin-top: .5rem;
    margin-bottom: 1rem;

}

button.card-custom p {
    width: 100%;
    margin-bottom: 0px;
}

.bolder {
    font-weight: bold;
}
</style>