<template>
    <div class="calendar col-12">
        <div id="header_button_group" class="d-flex">
            <button type="button" class="btn btn-light" @click="onClickToday">Hôm nay</button>
            <button type="button" class="btn btn-secondary" @click="onClickPrevWeek">Trước</button>
            <!-- get data from link -->
            <h3><span></span><strong>{{ this.listDate[0].Date }}/{{ this.listDate[0].Month }}/{{ this.listDate[0].Year
            }} – {{ this.listDate[6].Date
}}/{{ this.listDate[6].Month }}/{{ this.listDate[6].Year }}</strong></h3>
            <button type="button" class="btn btn-secondary" @click="onClickNextWeek">Sau</button>
        </div>
        <div class="table-wrapper-scroll-y my-custom-scrollbar col-12">
            <table class="table table-bordered">
                <thead>
                    <tr>
                        <th></th>
                        <th class="text-center" v-for="th in listDate" :key="th">{{ th.Day }},{{ th.Date }}/{{ th.Month
                        }}
                        </th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="td in listTime" :key="td">
                        <td>{{ td.time }}</td>
                        <td :class="{ attend: td.isSelected1 == 'attendance', absent: td.isSelected1 == 'absent' }">{{
                                td.message1
                        }}</td>
                        <td :class="{ attend: td.isSelected2 == 'attendance', absent: td.isSelected2 == 'absent' }">{{
                                td.message2
                        }}</td>
                        <td :class="{ attend: td.isSelected3 == 'attendance', absent: td.isSelected3 == 'absent' }">{{
                                td.message3
                        }}</td>
                        <td :class="{ attend: td.isSelected4 == 'attendance', absent: td.isSelected4 == 'absent' }">{{
                                td.message4
                        }}</td>
                        <td :class="{ attend: td.isSelected5 == 'attendance', absent: td.isSelected5 == 'absent' }">{{
                                td.message5
                        }}</td>
                        <td :class="{ attend: td.isSelected6 == 'attendance', absent: td.isSelected6 == 'absent' }">{{
                                td.message6
                        }}</td>
                        <td :class="{ attend: td.isSelected7 == 'attendance', absent: td.isSelected7 == 'absent' }">{{
                                td.message7
                        }}</td>
                    </tr>
                </tbody>
            </table>
        </div>
        <Teleport to="body">
            <LoaingPage v-show="this.$store.state.isLoading"></LoaingPage>
        </Teleport>
    </div>
</template>
<script>
import LoaingPage from "@/components/LoadingPage.vue";
import { DayOfWeek } from "@/constant/DayOfWeek";
import { FetchApiWithCustomHeader } from "@/api/APIFetch";
import { ContentType, Method, StudentAPI } from "@/api/ListAPI";
import { LocalStorageKey } from "@/constant/LocalStorageKey";

export default {
    name: "Schedule",
    props: {
        listDate: [],
        response: [],
    },
    watch: {
        response: function (newVal, oldVal) {
            console.log(oldVal);
            console.log("has changed");
            console.log(newVal);
            this.updateSlotList();
        },
        listDate: function(newVal,oldVal){
            console.log("listDate has change");
        }
    },
    created() {

    },
    methods: {
        Booking(row, col, value, message) {
            //console.log(row + " / " + col + " / " + value + " / " + message);
            switch (col) {
                case 1:
                    this.$data.listTime[row].isSelected1 = value;
                    this.$data.listTime[row].message1 = message;
                    break;
                case 2:
                    this.$data.listTime[row].isSelected2 = value;
                    this.$data.listTime[row].message2 = message;
                    break;
                case 3:
                    this.$data.listTime[row].isSelected3 = value;
                    this.$data.listTime[row].message3 = message;
                    break;
                case 4:
                    this.$data.listTime[row].isSelected4 = value;
                    this.$data.listTime[row].message4 = message;
                    break;
                case 5:
                    this.$data.listTime[row].isSelected5 = value;
                    this.$data.listTime[row].message5 = message;
                    break;
                case 6:
                    this.$data.listTime[row].isSelected6 = value;
                    this.$data.listTime[row].message6 = message;
                    break;
                case 7:
                    this.$data.listTime[row].isSelected7 = value;
                    this.$data.listTime[row].message7 = message;
                    break;
            }
        },
        updateLoading(newLoading) {
            this.$store.commit("setIsLoading", newLoading);
        },
        updateSlotList() {
            this.updateLoading(true);
            console.log(this.response);
            //let Date = this.response.startDate.split('-');
            this.updateLoading(false);
            //console.log("abc: " + this.response.length);
            for (let i = 0; i < this.response.length; i++) {
                //console.log(i + "/" + this.response[i].slotDate)
                for (let j = 0; j < this.listTime.length; j++) {
                    if (this.response[i].startTime.includes(this.listTime[j].time)) {
                        this.Booking(j, (this.response[i].slotDate - parseInt("1") + 1), this.response[i].attend, this.response[i].message);
                        if (parseInt(this.response[i].duration) > 0) {
                            for (let e = 1; e < parseInt(this.response[i].duration); e++) {
                                if (j + e < this.listTime.length) {
                                    this.Booking(j + e, (this.response[i].slotDate - parseInt("1") + 1), this.response[i].attend, this.response[i].message);
                                }
                            }
                        }
                    }
                }
            }
            this.updateLoading(false);
        },
        onClickNextWeek(){
            //alert("nextWeek");
            this.$emit("nextWeek");
        },
        onClickPrevWeek(){
            //alert("prevWeek");
            this.$emit("prevWeek");
        },
        onClickToday(){
            //alert("prevWeek");
            this.$emit("today");
        },

    },
    data() {
        return {
            listTime: DayOfWeek.LIST_SLOT_EMPTY,
        }
    },
    computed: {},
};
</script>

<style scoped>
#header_button_group {
    margin-top: 20px;
    margin-bottom: 10px;
}

#header_button_group button {
    margin-left: 10px;
    margin-right: 10px;
}

.table {
    background-color: white !important;
}

.my-custom-scrollbar {
    position: relative;
    height: 550px;
    overflow: auto;
}

.table-wrapper-scroll-y {
    display: block;
}

td.attend {
    background-color: aquamarine !important;
    color: black;
    text-align: center;
}

td.absent {
    background-color: lightcoral !important;
    color: black;
    text-align: center;
}
</style>
