<template>
    <div class="col-12 d-inline-flex">
        <div :class= "{'col-12': selectedDate == null, 'col-9': selectedDate != null}">
            <div id="header_button_group" class="d-flex" style="margin-top: 1rem;">
                <button type="button" class="btn btn-light" @click="GetAllDayOfMonth">Tháng hiện tại</button>
                <h3 class="bolder margin-lr">Tháng {{(this.dateObject.getMonth()+1)}} / {{dateObject.getFullYear()}}</h3>
                <button type="button" class="btn btn-secondary" @click="onClickNextMonth">Tháng Sau</button>
            </div>

            <div class="calendar col-12">
                <div class="container col-12">
                    <div class="item-head">
                        <button class="btn btn-secondary disabled" type="button">Thứ Hai</button>
                    </div>
                    <div class="item-head">
                        <button class="btn btn-secondary disabled" type="button">Thứ Ba</button>
                    </div>
                    <div class="item-head">
                        <button class="btn btn-secondary disabled" type="button">Thứ Tư</button>
                    </div>
                    <div class="item-head">
                        <button class="btn btn-secondary disabled" type="button">Thứ Năm</button>
                    </div>
                    <div class="item-head">
                        <button class="btn btn-secondary disabled" type="button">Thứ Sáu</button>
                    </div>
                    <div class="item-head">
                        <button class="btn btn-secondary disabled" type="button">Thứ Bảy</button>
                    </div>
                    <div class="item-head">
                        <button class="btn btn-secondary disabled" type="button">Chủ Nhật</button>
                    </div>
                </div>
                <div class="container col-12">
                    <div class="item" v-for="d in listDate" :key="d">
                        <button class="btn btn-light"
                            :class="{ disabled: d.month != this.dateObject.getMonth()+1, today: d.date == currentday && d.month == currentMonth, select: d.isSelected }"
                            type="button" @click="SelectTask(d)">
                            <p>{{ d.date }}/{{ d.month }}</p>
                            <p class="haveEvent"> Lịch học</p>
                        </button>
                    </div>
                </div>
            </div>
        </div>
        <div class="col-3">
            <div class="note" v-if="this.selectedDate!=null">
                <p class="small-size-text"> Ngày {{selectedDate.date}} tháng {{(this.selectedDate.month)}} năm {{this.selectedDate.year}}</p>
                <h3>Ghi chú:</h3>

                <div v-for="sl in listSlot" :key="sl" class="slot">
                    <p class="text-center">{{ sl.startTime }} - {{ sl.endTime }}</p>
                    <h4 class="bolder">{{ sl.Title }}</h4>
                    <p>{{ sl.content }}</p>
                </div>
                <div v-show="!addSlot">
                    <div class="col-12">
                        <button type="button" class="btn btn-light col-12" @click="onClickAdd">+</button>
                    </div>
                </div>


            </div>

        </div>
    </div>
    <div v-show="addSlot" class="AddSlotModal modal-bg">
        <div class="modal-dialog modal-dialog-centered modal modal-lg modal-child modal-dialog-scrollable"
            id="LoginModal">
            <div class="modal-content">
                <div class="modal-header d-flex justify-content-between">
                    <h5>Kết quả đánh giá ngày {{ date }}</h5>
                    <button class="btn btn-secondary" @click="onClickClose()">
                        &times;
                    </button>
                </div>
                <!-- Modal body -->
                <div class="modal-body">
                    <div class="col-12">
                        <div class="col-12 d-inline-flex">
                            <span class="col-4 text-center" style="padding-top: 10px;">Bắt đầu: &nbsp;</span>
                            <div class="col-8">
                                <input type="time" class="form-control" />
                            </div>
                        </div>
                        <div class="col-12 d-inline-flex">
                            <span class="col-4 text-center" style="padding-top: 10px;">Kết thúc: &nbsp;</span>
                            <div class="col-8">
                                <input type="time" class="form-control" />
                            </div>
                        </div>
                        <input type="text" class="form-control" placeholder=" Tiêu đề ..." />
                        <textarea type="text" class="form-control" placeholder=" Nội dung ..."></textarea>
                    </div>
                    <div class=" col-12 modal-footer d-flex justify-content-between">
                        <button type="button" class="btn btn-danger col-5" @click="onClickClose">Xóa</button>
                        <button type="button" class="btn btn-primary col-5">Lưu</button>

                    </div>
                </div>
            </div>
        </div>
    </div>

</template>
<script>
import { getCurrentInstance } from 'vue';
import Schedule from './Schedule.vue';

export default {
    name: "MonthSchedule",
    props: {

    },
    components: {

    },
    methods: {
        GetAllDayOfMonth() {
            const current = new Date();
            this.dateObject= new Date();
            let currentMonth = current.getMonth();
            // Back to Date 1
            current.setDate(1);
            current.setDate(current.getDate() - current.getDay() + 1);
            this.listDate = [];
            do {
                var len = this.listDate.length;
                this.listDate.push({ day: '', date: '', month: '', year: '' });
                this.listDate[len].day = current.getDay();
                this.listDate[len].date = current.getDate();
                this.listDate[len].month = current.getMonth() + 1;
                this.listDate[len].year = current.getFullYear();
                current.setDate(current.getDate() + 1);
            } while (current.getDate() != 1);
            var continueVar = true;
            while (continueVar) {
                var len = this.listDate.length;
                this.listDate.push({ day: '', date: '', month: '', year: '', isSelected: false });
                this.listDate[len].day = current.getDay();
                this.listDate[len].date = current.getDate();
                this.listDate[len].month = current.getMonth() + 1;
                this.listDate[len].year = current.getFullYear();
                current.setDate(current.getDate() + 1);
                if (current.getMonth() != currentMonth && current.getDay() == 0) {
                    continueVar = false;
                }
            }
        },
        onClickNextMonth() {
            const current = this.dateObject;
            // Back to Date 1
            this.dateObject.setDate(1);
            this.dateObject.setDate(current.getDate() + 33);
            
            let currentMonth = current.getMonth();
            current.setDate(1);
            this.listDate = [];
            if (current.getDay() == 0) {
                current.setDate(current.getDate() - 7);
            }
            current.setDate(current.getDate() - current.getDay() + 1);
            do {
                var len = this.listDate.length;
                this.listDate.push({ day: '', date: '', month: '', year: '' });
                this.listDate[len].day = current.getDay();
                this.listDate[len].date = current.getDate();
                this.listDate[len].month = current.getMonth() + 1;
                this.listDate[len].year = current.getFullYear();
                current.setDate(current.getDate() + 1);
            } while (current.getDate() != 1);
            var continueVar = true;
            while (continueVar) {
                var len = this.listDate.length;
                this.listDate.push({ day: '', date: '', month: '', year: '', isSelected: false });
                this.listDate[len].day = current.getDay();
                this.listDate[len].date = current.getDate();
                this.listDate[len].month = current.getMonth() + 1;
                this.listDate[len].year = current.getFullYear();
                current.setDate(current.getDate() + 1);
                if (current.getMonth() != currentMonth && current.getDay() == 0) {
                    continueVar = false;
                }
            }
            current.setDate(current.getDate()-7);
            current.setDate(1);
        },
        GetCurrentDayAndMonth() {
            const current = new Date();
            this.currentday = current.getDate();
            this.currentMonth = current.getMonth() + 1;
        },
        onClickAdd() {
            this.addSlot = true;
        },
        onClickClose() {
            this.addSlot = false;
        },
        SelectTask(main_item) {
            this.$data.listDate.forEach(element => {
                element.isSelected = false;
            });
            main_item.isSelected = true;
            this.selectedDate= main_item
        },
        getTotalClass() {
            if (selectedDate == null) {
                return "col-12"
            } else {
                return "col-9"
            }
        } 
    },
    created() {
        this.GetAllDayOfMonth();
        this.GetCurrentDayAndMonth();
    },
    data() {
        return {
            listDate: [],
            currentday: 1,
            currentMonth: 1,
            listSlot: [{ startTime: '14h30', endTime: '17h00', Title: 'Lịch học', content: 'học tại trung tâm' }],
            addSlot: false,
            dateObject: null,
            selectedDate: null,
        }
    }
}
</script>
<style scoped>
.item,
.item-head {
    display: inline-block;
    width: 14%;
    padding: 0.5%;
    margin: 0;


}

.item button {
    width: 100%;
    height: 5rem;
    font-weight: bolder;
    border: 1px solid #ccc;
}

.item-head button {
    width: 100%;
    height: 2.5rem;
    font-weight: bolder;
    border: 1px solid #ccc;
}

.margin-lr {
    margin-left: 3rem;
    margin-right: 3rem;
}

.calendar {
    border: 1px solid #ccc;
    margin-top: 2rem;
    margin-bottom: 1rem;
    border-radius: 20px;
}

.today {
    background-color: aquamarine !important;
    ;
}

.select{
    background-color: bisque!important;
}

.haveEvent {
    color: red;
}

.note {
    margin: 1rem;
    border: 1px solid #ccc;
    border-radius: 20px;
    width: 100%;
    height: 95%;
    padding: 1rem;
}

.small-size-text {
    font-size: 15px;
    color: black;
}

.slot {
    margin-bottom: 5px;
    padding: 3px;
    background-color: white;
    border: 1px solid #ccc;
    border-radius: 10px;
}

.AddSlotModal {
    /* let modal the only thing user can touch */
    position: fixed;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    z-index: 1;
    /* background of modal */
    background-color: rgba(100, 100, 100, 0.4);

    /* center modal */
    display: flex;
    justify-content: center;
    align-items: center;
}

.AddSlotModal input {
    margin-top: .5rem;
    margin-bottom: .5rem;
}
</style>